/**
 * Country Language Service (Frontend)
 * 
 * Mappt Lnder zu Sprachen fr Template-Auswahl basierend auf Reservation.guestNationality
 * 
 * Wiederverwendet die Logik aus backend/src/services/countryLanguageService.ts
 */
export class CountryLanguageService {
  /**
   * Mapping: Land-Name (verschiedene Schreibweisen) -> Sprachcode
   * 
   * Untersttzt:
   * - Spanische Namen (z.B. "Francia", "Colombia")
   * - Englische Namen (z.B. "France", "Colombia")
   * - Deutsche Namen (z.B. "Frankreich", "Kolumbien")
   */
  private static readonly COUNTRY_LANGUAGE_MAP: Record<string, string> = {
    // Spanisch-sprachige Lnder
    'colombia': 'es',
    'colombie': 'es',
    'mexico': 'es',
    'mxico': 'es',
    'argentina': 'es',
    'spain': 'es',
    'espaa': 'es',
    'espana': 'es',
    'chile': 'es',
    'peru': 'es',
    'per': 'es',
    'venezuela': 'es',
    'ecuador': 'es',
    'bolivia': 'es',
    'paraguay': 'es',
    'uruguay': 'es',
    'costa rica': 'es',
    'panama': 'es',
    'panam': 'es',
    'nicaragua': 'es',
    'honduras': 'es',
    'guatemala': 'es',
    'el salvador': 'es',
    'republica dominicana': 'es',
    'repblica dominicana': 'es',
    'cuba': 'es',
    'puerto rico': 'es',
    
    // Englisch-sprachige Lnder
    'united states': 'en',
    'usa': 'en',
    'united kingdom': 'en',
    'uk': 'en',
    'canada': 'en',
    'australia': 'en',
    'new zealand': 'en',
    'south africa': 'en',
    'ireland': 'en',
    'singapore': 'en',
    'philippines': 'en',
    'filipinas': 'en',
    
    // Deutsch-sprachige Lnder  Englisch (keine deutschen Templates)
    'germany': 'en',
    'alemania': 'en',
    'deutschland': 'en',
    'switzerland': 'es', // Schweiz: Spanisch (da hauptschlich spanisch-sprachige Gste)
    'suiza': 'es',
    'schweiz': 'es',
    'austria': 'en',
    'sterreich': 'en',
    
    // Franzsisch-sprachige Lnder
    'france': 'en', // Frankreich: Englisch als Fallback (keine franzsischen Templates)
    'francia': 'en',
    'frankreich': 'en',
    'belgium': 'en',
    'blgica': 'en',
    'belgien': 'en',
    'belgique': 'en',
    
    // Italienisch-sprachige Lnder
    'italy': 'en', // Italien: Englisch als Fallback
    'italia': 'en',
    'italien': 'en',
    
    // Portugiesisch-sprachige Lnder
    'brazil': 'en', // Brasilien: Englisch als Fallback (keine portugiesischen Templates)
    'brasil': 'en',
    'brasilien': 'en',
    'portugal': 'en',
    
    // Weitere Lnder (Fallback auf Englisch)
    'netherlands': 'en',
    'holanda': 'en',
    'niederlande': 'en',
    'poland': 'en',
    'polonia': 'en',
    'polen': 'en',
    'russia': 'en',
    'rusia': 'en',
    'russland': 'en',
    'china': 'en',
    'japan': 'en',
    'japn': 'en',
    'korea': 'en',
    'corea': 'en',
    'india': 'en',
    'turkey': 'en',
    'turqua': 'en',
    'trkei': 'en',
    'egypt': 'en',
    'egipto': 'en',
    'gypten': 'en',
    'bielorrusia': 'en', // Belarus: Englisch als Fallback
    'belarus': 'en',
  };

  /**
   * Bestimmt Sprachcode basierend auf Land-Name
   * 
   * Regel: Alle spanischsprachigen Lnder  'es', alle anderen  'en'
   * 
   * @param countryName - Land-Name (z.B. "Francia", "Colombia", "Germany")
   * @returns Sprachcode ('es' fr spanischsprachige Lnder, 'en' fr alle anderen) oder 'es' als Fallback
   */
  static getLanguageForCountry(countryName: string | null | undefined): string {
    if (!countryName) {
      return 'es'; // Fallback: Spanisch (Standard fr Kolumbien)
    }

    // Normalisiere: Kleinschreibung, entferne Leerzeichen
    const normalized = countryName
      .toLowerCase()
      .trim()
      .replace(/\s+/g, ' ');

    // Direktes Mapping
    if (this.COUNTRY_LANGUAGE_MAP[normalized]) {
      return this.COUNTRY_LANGUAGE_MAP[normalized];
    }

    // Fallback: Prfe ob Teilstring matcht (z.B. "United States" -> "united states")
    for (const [country, language] of Object.entries(this.COUNTRY_LANGUAGE_MAP)) {
      if (normalized.includes(country) || country.includes(normalized)) {
        return language;
      }
    }

    // Fallback: Spanisch (Standard fr Kolumbien)
    return 'es';
  }

  /**
   * Bestimmt Sprachcode fr Reservation basierend auf guestNationality
   * 
   * Prioritt:
   * 1. reservation.guestNationality (wenn vorhanden)
   * 2. Fallback: 'es' (Spanisch)
   * 
   * Regel: Alle spanischsprachigen Lnder  'es', alle anderen  'en'
   * 
   * @param reservation - Reservation mit guestNationality
   * @returns Sprachcode ('es' fr spanischsprachige Lnder, 'en' fr alle anderen)
   */
  static getLanguageForReservation(reservation: {
    guestNationality?: string | null;
  }): string {
    // Prioritt 1: Land-basierte Erkennung
    if (reservation.guestNationality) {
      const language = this.getLanguageForCountry(reservation.guestNationality);
      return language;
    }

    // Fallback: Spanisch
    return 'es';
  }
}

